import React from 'react';
import styled from 'styled-components';

const colorOrange = "#FFA341";

const sampleText = "This is cool test\nThis is another line of text";

export function ChatBox(props) {
    const texts = sampleText.split("\n").map(text => <p>{text}</p>);
    const Section = styled.section`
        margin: 0;
        background-color: ${colorOrange};
        padding: 1em;
        border-radius: 1.5em;
        max-width: 12em;
        font-size: 0.8em;
        white-space: pre-wrap;
    `;

    const Header = styled.header`
        font-size: 0.7em;
        padding: 0.5em 1em;
    `;

    return (
        <article>
            <Header>
                <span>{props.username}</span>
            </Header>
            <Section>
                {texts}
            </Section>
        </article>
    );
}
